	module.exports = {"data":{"challengeNode":{"title":"Problem 218: Perfect right-angled triangles","description":["Consider the right angled triangle with sides a=7, b=24 and c=25.","The area of this triangle is 84, which is divisible by the perfect numbers 6 and 28.","Moreover it is a primitive right angled triangle as gcd(a,b)=1 and gcd(b,c)=1.","Also c is a perfect square.","","We will call a right angled triangle perfect if","-it is a primitive right angled triangle","-its hypotenuse is a perfect square","","We will call a right angled triangle super-perfect if","-it is a perfect right angled triangle and","-its area is a multiple of the perfect numbers 6 and 28.","","","How many perfect right-angled triangles with c≤1016 exist that are not super-perfect?"],"challengeType":5,"fields":{"slug":"/coding-interview-prep/project-euler/problem-218-perfect-right-angled-triangles","blockName":"Project Euler","tests":[{"text":"<code>euler218()</code> should return 0.","testString":"assert.strictEqual(euler218(), 0, '<code>euler218()</code> should return 0.');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function euler218() {\n  // Good luck!\n  return true;\n}\n\neuler218();","head":"","tail":""},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/project-euler/problem-219-skew-cost-coding","id":"5900f4461000cf542c50ff59"},"slug":"/coding-interview-prep/project-euler/problem-218-perfect-right-angled-triangles"}}

